@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300&display=swap');

        *
        {
            margin: 0px;
            padding: 0px;
            font-family:'Times New Roman', Times, serif;
        }
        
        .newUser{
            text-align: center;
            font-weight: 500;
        }
        .newUser:hover{
            text-decoration: underline;
        }
        #logincontainer
        {
            /* background-image: linear-gradient(to bottom right,rgb(255, 0, 43),rgb(61, 1, 56));
            background-color: blueviolet; */
            background-color: rgb(149, 171, 236);
            height:100vh;
            width: 100%;
            display: flex;
            flex-wrap: wrap;
            justify-content: center ;
            align-content: center;
        }
       #loginbox
       {
           background-color: white;
           height: 500px;
           width: 800px;
           border-radius: 25px;
           box-shadow: 0px 0px 20px ;
           display: flex;
           flex-direction: row;
            flex-wrap: wrap;
            justify-content: space-evenly;
            align-content: center;
       }
       #loginlogo
       {
           height: 250px;
           width: 200px;
           background-color: tomato;
       }
       #h2comp1
       {
         font-family:Georgia, 'Times New Roman', Times, serif;
         font-weight: 600;
         text-align: center;

       }
       #loginmain
       {
        height: 250px;
           width: 400px;
           /* background-color:rgb(204, 204, 231); */
       }
       #login_man-sitting
       {
           height: 250px;
           width: 250px;
       }
       #loginbut
        {
            position: relative;
            height: 35px;
            width: 250px;
            border-radius: 20px;
            background-color: rgb(11, 150, 11);
            font-size: 15px;
            letter-spacing: 1.2px;
            color:rgb(247, 246, 248);
            position: relative;
            /* left: px; */
            font-weight: bold;
            border:none;
            outline: none;
            cursor: pointer;
        }
        #loginmain
        {
            display: flex;
           flex-direction: column;
            flex-wrap: wrap;
            justify-content: space-evenly;
            align-content: center;
            outline: none;
            
        }
        
        input#loginadmin
        {
            background-image:url("https://img.icons8.com/ios-glyphs/30/000000/new-post.png");
            background-position:15px;
            font-family:'Times New Roman', Times, serif;
            padding-left: 50px;
            background-repeat: no-repeat;
            background-size: 20px;
            width: 250px;
            z-index: 1;
            color: black;
            font-weight: 500;
            font-size: 1.2rem;
        }
        input#loginpass
        {
            background-image: url("https://img.icons8.com/ios-glyphs/30/000000/lock--v1.png");
            background-position:15px;
            background-repeat: no-repeat;
            font-family:'Times New Roman', Times, serif;
             padding-left: 50px;
            background-size: 20px;
            width: 250px;
            z-index: 1;
            color: black;
            font-weight: 500;
            font-size: 1.2rem;
        }
        .con
        {
            height: 35px;
            width: 200px;
            border-radius: 20px;
            background-color: lightgrey;
            border:none;
            outline: none;
            color: black;
        }
        a{
            color: black;
            text-decoration: none;
        }
